@font-face {
  font-family: 'IBM Plex Sans';
  src: url('/fonts/IBM_Flex_Sans/IBMPlexSans.eot');
  src: url('/fonts/IBM_Flex_Sans/IBMPlexSans.eot?#iefix') format('embedded-opentype'),
  url('/fonts/IBM_Flex_Sans/IBMPlexSans.woff2') format('woff2'),
  url('/fonts/IBM_Flex_Sans/IBMPlexSans.woff') format('woff'),
  url('/fonts/IBM_Flex_Sans/IBMPlexSans.ttf') format('truetype'),
  url('/fonts/IBM_Flex_Sans/IBMPlexSans.svg#IBMPlexSans') format('svg');
  font-weight: normal;
  font-style: normal;
}

